/* eslint-disable no-unused-vars */
import React, { useState, useEffect, useRef } from "react";
import { Link } from "react-router-dom";
import { Dropdown, Modal } from "react-bootstrap";
import toast from "react-hot-toast";
import { useWeb3React } from "@web3-react/core";
import Web3 from "web3";

import { toastSuccessParams } from "../../../assets/data/toast-params";
import { toastErrorParams } from "../../../assets/data/toast-params";
import { CompanyManagerAddress } from "../../blockchain/address";
import companyAbi from "../../blockchain/abi/MetaCompanyManager.json";
import { swithNetwork, injected, walletconnect } from "../ConnectWalletButton";
import Params from "../../blockchain/chainParams";

import metamaskicon from '../../../assets/images/blockchain/metamask.png';
import walleticon from '../../../assets/images/blockchain/walletConnectIcon.svg';
import { ReactComponent as Close } from '../../../assets/images/x.svg';


export const Navbar = ({ handleResponsive }) => {
  const unmounted = useRef(true);
  const { active, account, library, chainId, activate, deactivate} =
    useWeb3React();

  const [isCeoCompany, setIsCeoCompany] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [activatingConnector, setActivatingConnector] = useState();
  const [rewards, setRewards] = useState(0);
  const [network, setNetwork] = useState({});
  const [toggleMenu, setToggleMenu] = useState(false);

  let company;
  let chain_params;

  useEffect(() => {
    if (account) {
      chain_params = Params.find((e) => e.chainId == chainId)
      setNetwork(chain_params)
      if (CompanyManagerAddress[chain_params?.chainIdHex] !== undefined) {
        company = new library.eth.Contract(
          companyAbi,
          CompanyManagerAddress[chain_params.chainIdHex]
        ).methods;
        rewardPending()
        ceoCompany()
      } else {
        setNetwork({ chainName: "Wrong network" })
      }
      console.log({chain_params})
    }
  }, [account, isCeoCompany, rewards, chainId]);

  const claimreward = async (_) => {
    company
      .claimFromAllFranchisesBNB()
      .send({ from: account })
      .then((_) => {
        toast.success(
          "Congratulations! Claimed successfully.",
          toastSuccessParams
        );
      });
  };

  const ceoCompany = async (_) => {
    company
      .isCeo(account)
      .call()
      .then((res) => {
        setIsCeoCompany(res);
      });
  };

  const rewardPending = async (_) => {
    company
      .getMetaFranchisesUnclaimedRewardsBNB(account)
      .call()
      .then((res) => {
        setRewards(Web3.utils.fromWei(Number(res).toString(), "ether"));
      });
  };

  async function disconnect() {
    try {
      deactivate();
      localStorage.setItem("isWalletConnected", false);
      localStorage.removeItem('connector');
    } catch (ex) {
      console.log(ex);
    }
  }

  async function connectMetamaks() {
    try {
      setActivatingConnector("injected");
      console.log(activate(injected, undefined, true))
      await activate(injected, undefined, true);
      localStorage.setItem("connector", "injected")
      localStorage.setItem("isWalletConnected", true)
    } catch (ex) {
      toast.error("Please log into your wallet and select the Avalanche-C network");
    }
  }
  async function connectWalletConnect() {
    try {
      const config = walletconnect(chain_params)
      setActivatingConnector("walletconnect");
      await activate(config, undefined, true);
      console.log("sdfsdf", config, chainId)

      localStorage.setItem("connector", "walletConnect")
      localStorage.setItem("isWalletConnected", true)
    } catch (ex) {
      //toast.error("Unable to connect! Are you on the right network?");
    }
  }

  useEffect(() => {
    const connectWalletOnPageLoad = async () => {
      if (localStorage?.getItem("isWalletConnected") === "true") {
        try {
          if (localStorage.getItem("connector") === 'injected') {
            connectMetamaks();
          } else if (localStorage.getItem("connector") === 'walletConnect') {
            connectWalletConnect();
          }
        } catch (ex) {
          console.log(ex);
        }
      }
    };
    connectWalletOnPageLoad();
  }, []);

  const toggleNav = () => {
    setToggleMenu(!toggleMenu);
  };

  return (
    <>
      <nav className="navbar p-0 fixed-top d-flex flex-row">
        <div className="navbar-brand-wrapper d-flex d-lg-none align-items-center justify-content-center">
          <Link className="navbar-brand" to="/">
            <img
              className="responsive-hidden"
              src={require("../../../assets/images/logo_meta.png")}
              alt="logo"
            />
            <img
              className="screen-hidden"
              src={require("../../../assets/images/logo_meta.png")}
              alt="logo mini"
            />
          </Link>
        </div>
        <div className="navbar-menu-wrapper flex-grow d-flex align-items-stretch justify-content-md-end">
          <ul className="navbar-nav navbar-nav-right d-md-none d-lg-flex mt-0">

            <button
              type="button"
              className="btn btn-inverse-primary mr-3"
              onClick={() => {
                window.location = "/home";
              }}
            >
              Home
            </button>

            <button
              type="button"
              className="btn btn-inverse-primary mr-3"
              onClick={() => {
                window.location = "/nfts";
              }}
            >
              My NFTs
            </button>

            {/* <ConnectWalletButton responsive="pc" /> */}
            {active ? (
              <button
                type="button"
                className="btn btn-inverse-primary mr-3"
                onClick={disconnect}
              >
                <i className="mdi mdi-wallet"></i> ${account.slice(0, 5)}...$
                {account.slice(37)}
              </button>
            ) : (
              <button
                type="button"
                className="btn btn-success mr-3"
                // onClick={() => setShowModal(true)}
                onClick={connectMetamaks}
              >
                <i className="mdi mdi-wallet"></i>
                Connect Wallet
              </button>
            )}
             
          </ul>

          <button
            className="navbar-toggler-btn align-self-center"
            type="button"
            onClick={(_) => handleResponsive(true)}
          >
            <span className="mdi mdi-menu"></span>
          </button>
          <nav className="mobNav">
            {toggleMenu && (
              <ul className="list">
                <button
                  type="button"
                  className="btn btn-inverse-primary w-100"
                  onClick={() => {
                    window.open(
                      `${chain_params?.blockExplorerUrls[0]}/address/${CompanyManagerAddress[window.ethereum.chainId]
                      }#code`,
                      "name",
                      "noopener=yes"
                    );
                  }}
                >
                  <i className="mdi mdi-wallet"></i>
                  Connect Wallet
                </button>
                {account && isCeoCompany ? (
                  <li className="items" onClick={claimreward}>
                    Claim: {rewards}
                  </li>
                ) : (
                  ""
                )}

                {active ? (
                  <button
                    type="button"
                    className="btn btn-inverse-primary w-100"
                    onClick={disconnect}
                  >
                    <i className="mdi mdi-wallet"></i>${account.slice(0, 5)}...
                    {account.slice(37)}
                  </button>
                ) : (
                  <button
                    type="button"
                    className="btn btn-inverse-primary w-100"
                    onClick={() => setShowModal(true)}
                  >
                    <i className="mdi mdi-wallet"></i>
                    Connect Wallet
                  </button>
                )}
                <Dropdown
                  as="li"
                  className="nav-item d-lg-block switch-network-responsive"
                >
                  <Dropdown.Toggle className="nav-link btn btn-success create-new-button no-caret">
                    {chain_params ? (
                      chain_params?.chainName
                    ) : (
                      <>
                        <i className="mdi mdi-access-point-network"></i> Switch
                        Network
                      </>
                    )}
                  </Dropdown.Toggle>

                  <Dropdown.Menu className="navbar-dropdown preview-list create-new-dropdown-menu">
                    {Params.map((item, index) => (
                      <React.Fragment key={index}>
                        <Dropdown.Item
                          onClick={() => swithNetwork(item)}
                          className="preview-item"
                          active={chainId === item.chainId}
                        >
                          <div className="preview-item-content d-flex align-items-center">
                            <img
                              className="chain-navbar mr-2"
                              src={item.logo}
                              alt={item.chainName}
                            />
                            <p className="preview-subject mb-1">
                              {item.chainName}
                            </p>
                          </div>
                        </Dropdown.Item>
                        <Dropdown.Divider />
                      </React.Fragment>
                    ))}
                  </Dropdown.Menu>
                </Dropdown>
              </ul>
            )}
          </nav>
          <button
            className="navbar-toggler-btn align-self-center"
            type="button"
            onClick={toggleNav}
          >
            <span className="mdi mdi-format-line-spacing"></span>
          </button>
        </div>
      </nav>
      <Modal
        show={showModal}
        onHide={() => setShowModal(false)}
        className="custom-modal connect"
      // overlayClassName="custom-overlay"
      // closeTimeoutMS={500}
      >
        <div className="shane_tm_modalbox_news connectWallet">
          <div onClick={() => setShowModal(false)} className="div-close">
            <Close />
          </div>
          <h3>Connect a wallet</h3>
          <div className="modal_body">
            <div className="inner_body">
              <button onClick={connectMetamaks}>
                <div className="metamask">Metamask</div>
                <div className="icon">
                  <img
                    height={20}
                    width={20}
                    src={metamaskicon}
                    alt="connect with metamask"
                  />
                </div>
              </button>
              <button onClick={connectWalletConnect}>
                <div className="walletconnect">WalletConnect</div>
                <div className="icon">
                  <img
                    height={20}
                    width={20}
                    src={walleticon}
                    alt="connect with walletConnect"
                  />
                </div>
              </button>
            </div>
          </div>
        </div>
      </Modal>
    </>
  );
};

export default Navbar;
