import { InjectedConnector } from '@web3-react/injected-connector'
import { WalletConnectConnector } from "@web3-react/walletconnect-connector";
import Params from '../../blockchain/chainParams';

export const injected = new InjectedConnector({
  supportedChainIds: [97,56,137,80001,128,256,43114,43113,250,4002,10,79377087078960,1666600000],
})
export const walletconnect = async (params) => {
  return new WalletConnectConnector({
    rpcUrl: Params[0].rpcUrls[0],
    bridge: "https://bridge.walletconnect.org",
    qrcode: true
  })
};

const switchRequest = (param) => {
  if (window.ethereum)
    return window.ethereum.request({
      method: "wallet_switchEthereumChain",
      params: [{ chainId: param.chainIdHex }],
    });
};

const addChainRequest = (param) => {
  if (window.ethereum)
    // Switch to Testnet
    return window.ethereum.request({
      method: "wallet_addEthereumChain",
      params: [
        {
          chainId: param.chainId,
          chainName: param.chainName,
          rpcUrls: param.rpcUrls,
          blockExplorerUrls: param.blockExplorerUrls,
          nativeCurrency: param.nativeCurrency,
        },
      ],
    });
};

export const swithNetwork = async (param) => {
  // console.log("chainId", chainId);
  if (window.ethereum) {
    try {
      await switchRequest(param);
    } catch (error) {
      if (error.code === 4902) {
        try {
          await addChainRequest(param);
          await switchRequest(param);
        } catch (addError) {
          console.log(error);
        }
      }
      console.log(error);
    }
  }
};
