import React, { Component, Suspense, lazy } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import { AnimatePresence } from "framer-motion";

import Spinner from "./components/shared/Spinner";

// const Dashboard = lazy(() => import("./components/dashboard/Dashboard"));
// const Companies = lazy(() => import("./components/companies/Companies"));
// const Swap = lazy(() => import("./components/swap/Swap"));
//const Presale = lazy(() => import('./components/presale/Presale'));
// const LendBorrow = lazy(() => import("./components/lendborrow/Lendborrow"));
// const TradeView = lazy(() => import("./components/trade/TradeView"));
const Home = lazy(() => import("./components/home/Home"));
const Nfts = lazy(() => import("./components/nfts/Nfts"));

class AppRoutes extends Component {
  render() {
    return (
      <Suspense fallback={<Spinner />}>
        <AnimatePresence exitBeforeEnter>
          <Switch>
            <Route exact path="/home" component={Home} />
            <Route exact path="/nfts" component={Nfts} />
            <Redirect to="/home" />
          </Switch>
        </AnimatePresence>
      </Suspense>
    );
  }
}

export default AppRoutes;
